var parseFormatList = function parseFormatList(str) {
  if (!str) {
    return [];
  }

  var formatList;

  try {
    formatList = JSON.parse(str);
  } catch (error) {
    return [];
  }

  if (!Array.isArray(formatList)) {
    return [];
  }

  return formatList.filter(function (format) {
    return typeof format === "string";
  }); // ensure there are only strings in list
};

var parseTracks = function parseTracks(str) {
  if (!str) {
    return [];
  }

  var tracks;

  try {
    tracks = JSON.parse(str);
  } catch (error) {
    return [];
  }

  if (!Array.isArray(tracks)) {
    return [];
  }

  return tracks.filter(function (track // ensure required fields are present
  ) {
    return track.hasOwnProperty("user") && track.hasOwnProperty("username") && track.hasOwnProperty("date");
  });
};

var blockLevelToDOM = function blockLevelToDOM(node) {
  var attrs = node.attrs.track.length ? {
    class: node.attrs.class,
    "data-track": JSON.stringify(node.attrs.track)
  } : {
    class: node.attrs.class
  };
  return attrs;
};

